@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

a {
  text-decoration: none;
}

*:focus {
  outline: none;
}

html {
  width: 100%;
}

body {
  color: #ffffff;
  font-family: "Rubik Mono One", sans-serif;
  margin: 0;
  font-size: 1vw;
  background: #24186a;
  width: 100%;
}

.container {
  width: 97.91667vw;
  margin: 0 auto;
  position: relative;
}

.social-block .soc-link {
  border: none;
  background: #f04132;
  width: 2.60417vw;
  height: 2.60417vw;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.social-block .soc-link svg {
  width: 1.04167vw;
  height: 1.04167vw;
  object-fit: contain;
}

.social-block .soc-link svg * {
  fill: white;
}

.social-block .soc-link:hover {
  background: #cb382e;
}

.social-block .soc-link + .soc-link {
  margin-top: 0.52083vw;
}

.title span {
  color: white;
  display: inline-block;
}

.title span.yellow {
  background-color: #fdb315;
}

.title span.blue {
  background-color: #24186a;
}

.title span.red {
  background-color: #f04132;
}

.text_style {
  color: #ffffff;
  font-family: "Montserrat";
  font-size: 1.25vw;
  font-weight: 500;
  line-height: 1.40625vw;
}

.text_style p + p {
  margin-top: 1.30208vw;
}

.section_one {
  height: 56.25vw;
  background: url("../images/bg-top.jpg") left center no-repeat;
  background-size: cover;
  position: relative;
}

.section_one::before {
  content: "";
  clip-path: polygon(28% 0, 100% 0, 100% 100%, 0% 100%);
  background: #f04132;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 47.70833vw;
  pointer-events: none;
}

.section_one .container {
  height: 100%;
}

.section_one .logo {
  display: block;
  position: absolute;
  top: 1.92708vw;
  left: 1.30208vw;
  width: 17.55208vw;
}

.section_one .logo img {
  width: 100%;
  display: block;
}

.section_one .social-block {
  position: absolute;
  left: 1.30208vw;
  bottom: 1.97917vw;
}

.section_one .maxim {
  display: block;
  width: 12.08333vw;
  position: absolute;
  right: 0;
  top: 1.19792vw;
}

.section_one .maxim img {
  width: 100%;
  display: block;
}

.section_one .daa {
  display: block;
  width: 15.41667vw;
  position: absolute;
  left: 58.33333vw;
  top: 2.86458vw;
}

.section_one .daa img {
  width: 100%;
  display: block;
}

.section_one .right {
  padding-top: 17.34375vw;
}

.section_one .right .title {
  text-align: right;
  font-size: 5.46875vw;
  font-weight: 400;
  line-height: 0.8;
}

.section_one .right .title span {
  padding: 1.30208vw 1.30208vw 1.14583vw;
}

.section_one .right .text {
  width: 39.58333vw;
  margin-top: 1.5625vw;
  text-align: right;
  margin-left: auto;
}